___CSS_LOADER_EXPORT___.push([module.id, `.MuiDataGrid-root{
  border-radius: 0.5rem !important;
  border: 1px solid #266899 !important;
  background: #FFF;
  box-shadow: 0px 2px 4px -10px rgba(0, 0, 0, 0.10);  
}

.MuiDataGrid-row.Mui-selected {
  background-color: rgba(57, 164, 247, 0.10) !important;
}

.MuiDataGrid-cellCheckbox{
  border-color: #E2E2E2 !important;
}

.MuiDataGrid-row:hover, .MuiDataGrid-row.Mui-hovered {
  background-color: rgba(57, 164, 247, 0.10) !important;
}
.MuiDataGrid-columnHeader{
  color: #266899;
  font-size: 0.75rem;
  font-style: normal;
  font-weight: 500;
  line-height: 120%; /* 0.9rem */
  letter-spacing: 0.0225rem;
  border-right: 1px solid #266899 !important;
  position: relative;
}

.MuiDataGrid-columnHeaders{
  position: relative;
}

.MuiDataGrid-columnHeaders:after{
  position: absolute;
  content: '';
  left: 0;
  right: 0;
  bottom: 0px;
  height: 1px;
  background: #266899;
}

/* .MuiDataGrid-virtualScrollerRenderZone>*[data-rowindex="0"]{
  border-top: 1px solid #266899 !important;
} */

.MuiDataGrid-row>.MuiDataGrid-cell, .MuiDataGrid-columnHeader {
  min-height: 40px !important;
  max-height: 40px !important;
}

.MuiDataGrid-row>.MuiDataGrid-cell+.MuiDataGrid-cell{
  border-left: 1px solid #E2E2E2 !important;
}
.MuiDataGrid-row>.MuiDataGrid-cell::nth-last-child(2){
  border-left: transparent !important;
}

.MuiDataGrid-row>.MuiDataGrid-cell{
  border-color: #E2E2E2;
}

.MuiDataGrid-columnHeaderTitle{
  font-size: 12px;
  font-weight: 500 !important;
  letter-spacing: 0;
}

.MuiDataGrid-row, .MuiDataGrid-columnHeaders{
  min-height: unset !important;
  max-height: unset !important;
}

.MuiDataGrid-row:nth-child(even){
  background: #F3F8FB;
}

.MuiDataGrid-cellContent{
  color:#060606 ;
}

.MuiDataGrid-cellCheckbox .MuiCheckbox-root, .MuiDataGrid-columnHeaderTitleContainerContent .MuiCheckbox-root{
  transform: scale(0.9) !important;
}

.MuiDataGrid-columnHeader:focus, .MuiDataGrid-cell:focus {
  outline: solid #000000 1px !important;
  outline-offset: -1px;
}
/* .MuiDataGrid-footerContainer{
  display: none !important;
} */
.MuiDataGrid-virtualScrollerContent{
  height: auto !important;
}`, "",{"version":3,"sources":["webpack://./src/business-components/WSDataGrid/WSDataGrid.css"],"names":[],"mappings":"AAEA;EACE,gCAAgC;EAChC,oCAAoC;EACpC,gBAAgB;EAChB,iDAAiD;AACnD;;AAEA;EACE,qDAAqD;AACvD;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,qDAAqD;AACvD;AACA;EACE,cAAc;EACd,kBAAkB;EAClB,kBAAkB;EAClB,gBAAgB;EAChB,iBAAiB,EAAE,WAAW;EAC9B,yBAAyB;EACzB,0CAA0C;EAC1C,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,OAAO;EACP,QAAQ;EACR,WAAW;EACX,WAAW;EACX,mBAAmB;AACrB;;AAEA;;GAEG;;AAEH;EACE,2BAA2B;EAC3B,2BAA2B;AAC7B;;AAEA;EACE,yCAAyC;AAC3C;AACA;EACE,mCAAmC;AACrC;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,eAAe;EACf,2BAA2B;EAC3B,iBAAiB;AACnB;;AAEA;EACE,4BAA4B;EAC5B,4BAA4B;AAC9B;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,qCAAqC;EACrC,oBAAoB;AACtB;AACA;;GAEG;AACH;EACE,uBAAuB;AACzB","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap');\n\n.MuiDataGrid-root{\n  border-radius: 0.5rem !important;\n  border: 1px solid #266899 !important;\n  background: #FFF;\n  box-shadow: 0px 2px 4px -10px rgba(0, 0, 0, 0.10);  \n}\n\n.MuiDataGrid-row.Mui-selected {\n  background-color: rgba(57, 164, 247, 0.10) !important;\n}\n\n.MuiDataGrid-cellCheckbox{\n  border-color: #E2E2E2 !important;\n}\n\n.MuiDataGrid-row:hover, .MuiDataGrid-row.Mui-hovered {\n  background-color: rgba(57, 164, 247, 0.10) !important;\n}\n.MuiDataGrid-columnHeader{\n  color: #266899;\n  font-size: 0.75rem;\n  font-style: normal;\n  font-weight: 500;\n  line-height: 120%; /* 0.9rem */\n  letter-spacing: 0.0225rem;\n  border-right: 1px solid #266899 !important;\n  position: relative;\n}\n\n.MuiDataGrid-columnHeaders{\n  position: relative;\n}\n\n.MuiDataGrid-columnHeaders:after{\n  position: absolute;\n  content: '';\n  left: 0;\n  right: 0;\n  bottom: 0px;\n  height: 1px;\n  background: #266899;\n}\n\n/* .MuiDataGrid-virtualScrollerRenderZone>*[data-rowindex=\"0\"]{\n  border-top: 1px solid #266899 !important;\n} *